import React from "react";
import Menu from "./Menu";
import Categories from "./Categories";
import data from "./data";

const categories = ["all", ...new Set(data.map((item) => item.category))];
console.log(categories);

function App() {
  const [items, setItems] = React.useState(data);
  const [catgrs, setCatgrs] = React.useState(categories);
  const filterMenu = (catgr) => {
    if (catgr === "all") return setItems(data);
    else {
      const newItems = data.filter((item) => item.category === catgr);
      setItems(newItems);
    }
  };
  return (
    <main>
      <section className="menu section">
        <div className="title">
          <h2>Menu</h2>
          <div className="underline"></div>
          <Categories categories={catgrs} filter={filterMenu} />
          <Menu items={items} />
        </div>
      </section>
    </main>
  );
}

export default App;
